import PropTypes from "prop-types";
import { useLocation, useParams } from "react-router-dom";
import { useEffect, useState } from "react";


import Footer from "../components/Footer";
import MainHeader from "../components/MainHeader";
import ProjectList from "../components/ProjectList";
import ProjectMenu from "../components/ProjectMenu";

import '../styles/default_layout.css'
import IssueSearch from "../components/IssueSearch";
import IssueList from "../components/IssueList";
import { APIURL } from "../source/constants";
import axios from "axios";
import { jwtDecode } from "jwt-decode";



function Filter({userInfo, setUserInfo}) {
    const {projectId} = useParams();
    const [loading, setLoading] = useState(true);

    // issue 
    const [issues, setIssues] = useState([]);

    //search  
    const [searchInfo, setSearchInfo] = useState({
        title : "",
        priority : "",
        state : "",
        category : "",
        issueCount : "",
        reporter : "",
        fixer : "",
        assignee : "",
    });

    //form   handling
    const handleChange = (event) => {
        const {name, value} = event.target;
        setSearchInfo({
            ...searchInfo,
            [name] : value
        });
    };

    //form  handling
    const handleSubmit = async (e) => {
        e.preventDefault();

        let searchCondition = {};

        Object.keys(searchInfo).forEach(key => {
            if (searchInfo[key] !== "") {
                searchCondition[key] = searchInfo[key];
            }
        });

        const searchedIssues = await axios.get(`${APIURL}/projects/${projectId}/issues`,{
            headers : {
                'Authorization' : userInfo.JWT
            },
            params : searchCondition
        })

        setIssues(searchedIssues.data.map(issue => ({
            state : issue.state,
            priority : issue.priority,
            title : issue.title,
            issueId : issue.issueId,
        })));

        try {


        }catch(error) {
            console.error(error);
        }
    }


    //issues  
    //  
    const fetchIssues = async () => {
        setLoading(true);

        try {

            var fetchCondition = {};

            //   
            const issueNumber = 20;
            const aId = jwtDecode(userInfo.JWT).accountId
            const response = await axios.get(`${APIURL}/users/${aId}/project/${projectId}/role`);

            //  
            switch(response.data){
                case 'Administrator' : //admin   
                    fetchCondition = {
                        issueCount : issueNumber,
                    };
                    break;
                case 'ProjectLeader' :  //PL NEW state issue   
                    fetchCondition = {
                        state : "NEW",
                        issueCount : issueNumber,
                    };
                    break;
                case 'Developer' : //Developer  assignee  issue 
                    fetchCondition = {
                        assignee : aId,
                        issueCount : issueNumber,

                    };
                    break;
                case 'Tester' : //Tester,  reporter issue 
                    fetchCondition = {
                        reporter : aId,
                        issueCount : issueNumber,

                    };
                    break;
            }

            // 
            const searchedIssues = await axios.get(`${APIURL}/projects/${projectId}/issues`,{
                headers : {
                    'Authorization' : userInfo.JWT
                },
                params : fetchCondition
            })

            setIssues(searchedIssues.data.map(issue => ({
                state : issue.state,
                priority : issue.priority,
                title : issue.title,
                issueId : issue.issueId,
            })));

            

            console.log(issues);
            


        }catch(error) {
            console.error(error);
        }finally{
            setLoading(false);
        }
    }

    useEffect(()=> {
        fetchIssues();
    }, []);




    if(loading){
        return(
            <div>Loading...</div>
        )
    }


    return (
        <div>
            <MainHeader userInfo={userInfo} setUserInfo={setUserInfo}/>
            <div class="center_area">
                <ProjectList />
                <main class="project_area">
                    <ProjectMenu />
                    <section class="main_section">
                        <IssueSearch 
                            title = {searchInfo.title}
                            issueCount={searchInfo.issueCount}
                            priority={searchInfo.priority}
                            state={searchInfo.state}
                            category={searchInfo.category}
                            reporter={searchInfo.reporter}
                            fixer={searchInfo.fixer}
                            assignee={searchInfo.assignee}
                            handleChange={handleChange}
                            handleSubmit={handleSubmit}
                        />
                        <IssueList 
                            Issues={issues}
                        />
                    </section>
                </main>
            </div>
            <Footer />
        </div>
    );
}
Filter.propTypes = {
    userInfo : PropTypes.object.isRequired,
    setUserInfo : PropTypes.func,
};


export default Filter;

